body {
  background-color: #e6e7ee;
}

h2 {
  text-align: center;
}

.main_header {
  width: 100%;
  height: 450px;
  display: flex;
  align-items: center;
  flex-direction: column;
  background: url(./img/bkg.jpg);
  background-size: 100% auto;
}

.visit_card {
  position: relative;
  width: 220px;
  height: 120px;
  padding: 10px 15px 15px 10px;
  margin-top: 40px;
  border-radius: 2px;
  background: url(./img/texture.jpg);
  background-size: cover;
  box-shadow: 
  /* top shadow */
  0px -10px 10px rgba(0,0,0,0.2),
  /* bottom shadow */
  0px 10px 20px rgba(0,0,0,0.4),
  /* left shadow */
  -10px 0px 20px rgba(0,0,0,0.4),
  /*right shadow */
  10px 0px 20px rgba(0,0,0,0.4),
  /* inner shadow */
  inset 1px 1px 20px rgba(0,0,0,0.4)
  ;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  transition: 0.5s;
}

.logo {
  width: 20px;
  height: 20px;
  background: url(./img/gold-texture.jpg);
  background-size: cover;
  mask: url(./img/logo.png);
  -webkit-mask: url(./img/logo.png);
}

.card_title {
  font-size: 20px;
  letter-spacing: 1px;
  word-spacing: 3px;
  color: #b4b4b4;
  text-shadow: -1px -1px 1px rgba(0,0,0,0.1);
  text-align: center;
}

.underline {
  width: 180px;
  height: 3px;
  background: linear-gradient( 150deg, rgba(92, 92, 92, 0.3) 0%, rgba(37, 37, 37, 0.8) 100%);
  background-color: #0091FF;
  margin-top: 5px;
}

.back .card_title {
  font-size: 14px;
  word-spacing: 3px;
  color: #b4b4b4;
}

.card_text {
  font-size: 12px;
  color: #0091FF;
}

.visit_card.back .right {
  position: absolute;
  width: 200px;
  right: 10px;
  bottom: 20px;
  font-size: 10px;
  font-weight: 700;
  color: #b4b4b4;
}

.visit_card.back .left {
  position: absolute;
  left: 30px;
  top: 25px;
  color: transparent;
  font-weight: 700;
}

.visit_card.front {
  transform: rotateZ(-7deg);
  left: -5%;
}

.visit_card.back {
  transform: rotateZ(4deg);
  left: 5%;
  top: -2%;
}

.info_cards {
  width: 100%;
  padding-top: 20px;
}

.info_cards_wrapper {
  text-align: center;
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}

.info_card {
  width: 40%;
  border-radius: 5px;
  box-shadow: 6px 6px 6px 0 rgba(0, 0, 0, 0.5),
    -6px -6px 15px 0 rgba(255, 255, 255, 0.897);
  box-sizing: border-box;
  padding: 10px;
  margin-bottom: 20px;
}

.info_card_text {
  font-size: 10px;
  font-weight: 700;
}

.how_to_wraper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 80vh;
  position: relative;
  overflow: hidden;
  margin-left: auto;
  margin-right: auto;
}

.how_to_card {
  position: absolute;
  width: 80%;
  min-height: 500px;
  transition: transform 0.5s ease-in-out;
  -webkit-transform: translateX(0%);
  transform: translateX(0%);
  box-shadow: 6px 6px 6px 0 rgba(0, 0, 0, 0.5),
  -6px -6px 15px 0 rgba(255, 255, 255, 0.897);
  border-radius: 5px;
}

.how_to_move_right {
  -webkit-transform: translateX(600px);
  transform: translateX(600px);
}

.how_to_move_left {
  -webkit-transform: translateX(-600px);
  transform: translateX(-600px);
}

.text {
  position: absolute;
  width: 80%;
  left: 15px;
  top: 10px;
  font-size: 15px;
  font-weight: 700;
}

.invisible {
  display: none;
}

.active_slide {
  display: block;
}

.how_to_left {
  width: 5%;
  /* border: 1px solid red; */
  position: absolute;
  top: 0;
  left: 10px;
  height: 100%;
  color: #0091FF;
  text-align: center;
  align-content: center;
  padding-top: 40vh;
  box-sizing: border-box;
}

.how_to_right {
  /* border: 1px solid green; */
  width: 20px;
  height: 20px;
  border-radius: 50px;
  width: 5%;
  position: absolute;
  top: 0;
  right: 10px;
  height: 100%;
  color: #0091FF;
  text-align: center;
  padding-top: 40vh;
  box-sizing: border-box;
}

.examples_wraper {
  width: 100%;
}

.examples_container {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}

.example_img {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
}

.example_back {
  position: relative;
  width: 262px;
  height: 320px;
  overflow: hidden;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 6px 6px 6px 0 rgba(0, 0, 0, 0.5),
    -6px -6px 15px 0 rgba(255, 255, 255, 0.897);
  box-sizing: border-box;
}

.example_front {
  position: absolute;
  width: 100%;
  overflow: hidden;
  top: 0;
  left: 0;
  /* transition: all 0.1s ease-out; */
}

.example_range {
  position: absolute;
  z-index: 2;
  top: 0;
  bottom: 0;
  height: 100%;
  margin: 0;
  left: -1px;
  width: calc(100% + 2px);
  cursor: pointer;
  /* -webkit-appearance: slider-horizontal !important; */
  -moz-appearance: none;
  opacity: 0;
  -ms-touch-action: auto;
  touch-action: auto;
}

.ex_handle {
  position: absolute;
  z-index: 2;
  pointer-events: none;
  opacity: 1;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  color: #0091FF;
  background: rgba(255, 255, 255, 0.5);
  width: 48px;
  height: 48px;
  border-radius: 50%;
  box-shadow: 6px 6px 6px 0 rgba(0, 0, 0, 0.5),
  -6px -6px 15px 0 rgba(255, 255, 255, 0.1);
  /* box-shadow: 0 0 6px rgba(0, 0, 0, 0); */
  transition: background 0.3s, box-shadow 0.3s, opacity 0.5s 0.25s;
  box-sizing: border-box;
}

.ex_handle::before {
  left: 10px;
  transform: rotate(-45deg);
  content: '';
  position: absolute;
  width: 10px;
  height: 10px;
  top: 50%;
  border-top: solid 2px;
  border-left: solid 2px;
  transform-origin: 0 0;
}

.ex_handle::after {
  right: 0;
  transform: rotate(135deg);
  content: '';
  position: absolute;
  width: 10px;
  height: 10px;
  top: 50%;
  border-top: solid 2px;
  border-left: solid 2px;
  transform-origin: 0 0;
}

.slider_list {
  width: 90%;
/*   border: 1px solid blue; */
  border-radius: 15px;
  height: 160px;
  box-shadow: inset 3px 3px 10px 0 rgba(0, 0, 0, 0.4),
  inset -3px -3px 10px 0 rgba(255, 255, 255, 0.3);
  display: flex;
  align-items: center;
  box-sizing: border-box;
  overflow-x: auto;
  scrollbar-width: none;
  margin-left: auto;
  margin-right: auto;
  margin-top: 25px;
}

.slider_list::-webkit-scrollbar {
  display: none;
}

.slider_list_item {
  min-width: 100px;
  max-width: 100px;
  margin-left: 25px;
  height: 124px;
  box-shadow: 12px 12px 12px 0 rgba(0, 0, 0, 0.2),
    -6px -6px 15px 0 rgba(255, 255, 255, 0.5),
    10px 10px 10px 0 rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

.test_img {
  width: 100%;
}

.price_list_wrapper {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: #e6e7ee;
  padding-bottom: 50px;
}

.price_table {
  width: 85%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  box-shadow: 12px 12px 12px 0 rgba(0, 0, 0, 0.2),
    -6px -6px 15px 0 rgba(255, 255, 255, 0.5),
    10px 10px 10px 0 rgba(0, 0, 0, 0.2);
  border: 1px solid #d1d9e6;
  border-radius: 10px;
}

.price_string {
  display: flex;
  width: 100%;
  padding: 10px;
  border-bottom: 1px solid #d1d9e6;
}

.price_item {
  display: block;
  width: 75%;
}

.price {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 25%;
  text-align: center;
  border-left: 1px solid #0091FF;
  height: 100%;
}
